import React from "react";
import PropTypes from "prop-types";

import st from "./style";
// import Link from 'gatsby';

class ClckableOpacity extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      hover: false
    };
  }

  handleClick = () => {
    if (this.props.disabled) {
      return;
    }
    this.props.onClick();
  };

  onMouseOver = () => {
    this.setState({
      hover: true
    });
  };

  onMouseOut = () => {
    this.setState({
      hover: false
    });
  }; 

  render() {

    const CustomTag = this.props.type || 'button';

    return (
      <CustomTag
        onClick={this.handleClick}
        onMouseOver={this.onMouseOver}
        onMouseOut={this.onMouseOut}
        style={{
          ...st.container(this.props),
          ...this.props.style
        }}
        href={this.props.href}
      >
        {this.props.children}
      </CustomTag>
    
    );
  }
}

ClckableOpacity.propTypes = {
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  type: PropTypes.string,//button || a || Link
  href: PropTypes.string,
  style: PropTypes.object,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.node,
    PropTypes.array,
  ]),
};
ClckableOpacity.defaultProps = {
  onClick: () => {},
  disabled: false,
  type: 'button',
  href: null,
  style: {},
  children: null,
};

export default ClckableOpacity;
